import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { WorkgroupComponent } from './workgroup.component';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';

const routes: Routes = [
  {
    path: '',
    component: WorkgroupComponent
  },
  {
    path: 'byhour',
    loadChildren: () => import('./byhour/byhour.module').then(m => m.ByhourModule)
  },
  {
    path: 'byshift',
    loadChildren: () => import('./byshift/byshift.module').then(m => m.ByshiftModule)
  }
]

@NgModule({
  declarations: [WorkgroupComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    FormsModule,
    ReactiveFormsModule.withConfig({ warnOnNgModelWithFormControl: 'never' }),
    TranslateModule
  ]
})
export class WorkgroupModule { }
