import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAuth } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-workgroup',
  templateUrl: './workgroup.component.html',
  styleUrls: ['./workgroup.component.scss']
})
export class WorkgroupComponent implements OnInit {

  constructor(
    private vhAuth: VhAuth,
    private lang: LanguageService,
    private vhComponent: VhComponent,
    private router: Router
  ) {

  }
  ngOnInit() {

  }
  gotoByhour() {
    this.router.navigate(['/cafe/dashboard/management/partner/employee/workgroup/byhour']);
  }
  gotoByshift() {
    this.router.navigate(['/cafe/dashboard/management/partner/employee/workgroup/byshift']);
  }
  goBack() {
    this.router.navigate(['/cafe/dashboard/management/partner/employee']);
  }
}
